<template>
  <div class="TestCase">
    <!-- TestCase -->
    <div class="q-pa-md">

      <transition name="TreeEntity-slid">
        <TreeEntity v-if="expanded"
                    ref="TreeEntity"
                    style="max-width:20%;height:100%;overflow:auto;float:left;"
                    @getDirectoryLocation="getDirectoryLocation"
                    :DirectoryLocation="DirectoryLocation" />
      </transition>

      <div style="height:100%;">
        <q-btn color="grey"
               flat
               dense
               style="width:2%;height:100%;float:left;"
               :icon="expanded ? 'keyboard_arrow_left' : 'keyboard_arrow_right'"
               @click="expanded = !expanded" />

        <q-table title=""
                 :data="TestCaseList"
                 :columns="columns"
                 row-key="id"
                 selection="multiple"
                 :selected.sync="selected"
                 :rows-per-page-options=[0]
                 no-data-label="">

          <template v-slot:top-right>
            <q-btn class="btn"
                   color="primary"
                   label=" "
                   @click="openRunModel" />
            <q-btn class="btn"
                   color="primary"
                   label=" "
                   @click="openCrateTestCase" />
          </template>
          <template v-slot:bottom
                    class="row">
            <q-pagination v-model="pagination.page"
                          :max="pagination.rowsNumber"
                          :input="true"
                          class="col offset-md-10"
                          @input="nextPage">
            </q-pagination>
          </template>
          <template v-slot:body-cell-id="props">
            <q-td class=""
                  :props="props">
              <q-btn class="btn"
                     color="primary"
                     v-show="props.row.status==''"
                     label=""
                     @click="lookMasterLog(props)" />
              <q-btn class="btn"
                     color="primary"
                     label=" "
                     @click="toDetail(props)" />
              <q-btn class="btn"
                     style="background: #FF0000; color: white"
                     label=" "
                     @click="deleteTestCase(props)" />
            </q-td>
          </template>
        </q-table>
      </div>

    </div>

    <!-- TestCase -->
    <q-dialog v-model="createFixed"
              persistent>
      <q-card style="width: 100%; max-width: 70vw;">
        <q-card-section>
          <div class="text-h6"></div>
        </q-card-section>

        <q-separator />

        <CreateShowTestCase :masterHostList="masterHostList"
                            :currentDirectory="SelectLocation"
                            ref="CSTestCase" />

        <q-separator />

        <q-card-actions align="right">
          <q-btn flat
                 label=""
                 color="primary"
                 @click="newCancel" />
          <q-btn flat
                 label=""
                 color="primary"
                 @click="newCreate" />
        </q-card-actions>
      </q-card>
    </q-dialog>

    <!--  -->
    <!-- <q-dialog v-model="runFixed"
              position='left'
              persistent>
      <q-card style="width: 100%; max-width: 50vw;">
        <q-card-section>
          <div class="text-h6"></div>
        </q-card-section>

        <q-separator />
        <div class="q-pa-md">
          <q-radio v-model="runModel"
                   val="parallel"
                   label="" />
          <q-radio v-model="runModel"
                   val="sequential"
                   label="" />

          <div v-show="runModel=='parallel'">

            <div class="input_row row"
                 style="margin-bottom:10px;width:100%;display:inlin-block;"
                 v-for="(value,index) in runModelArray"
                 :key="index">
              <q-input v-model="value.executionTime"
                       outlined
                       class="col-8"
                       :dense="true"
                       placeholder=""
                       @input="forceUpdate(value.executionTime,index)">
                <template v-slot:before>
                  <span style="font-size:14px;width:150px;word-wrap:break-word;">{{value.name}}:</span>
                </template>
                <template v-slot:append>
                  <q-avatar>
                    
                  </q-avatar>
                </template>
              </q-input>

            </div>

          </div>
        </div>
        <q-separator />

        <q-card-actions align="right">
          <q-btn flat
                 label=""
                 color="primary"
                 @click="runCancelTestCase" />
          <q-btn flat
                 label=""
                 color="primary"
                 @click="runTestCase" />
        </q-card-actions>
      </q-card>
    </q-dialog> -->

    <MixedRunTest ref="MixedRunTest"
                  :runFixedSH="runFixed"
                  :selectedArr="selected" />
  </div>
</template>

<script>
import * as Apis from "@/api/index"
import CreateShowTestCase from './component/CreateShowTestCase.vue' //
import TreeEntity from "@/components/TreeEntity.vue"                //
import MixedRunTest from "./component/MixedRunTest.vue"             //
export default {
  name: 'TestCase',
  components: {
    TreeEntity,
    CreateShowTestCase,
    MixedRunTest
  },
  data () {
    return {
      createFixed: false,   //flag
      TestCaseList: [],     //TeseCase
      masterHostList: [],//
      dataSourceName: [],//

      selected: [],   //
      //
      columns: [
        {
          name: 'name',
          required: true,
          label: '',
          align: 'left',
          field: row => row.name,
          format: val => `${val}`,
        },
        { name: 'engineType', align: 'left', label: '', field: 'engineType', style: 'max-width: 50px', headerStyle: 'max-width: 50px' },
        { name: 'configuration', label: '', align: 'left', field: 'configuration', style: 'max-width: 250px', headerStyle: 'max-width: 250px' },
        { name: 'status', label: '', align: 'left', field: 'status', },
        { name: 'id', label: '', align: 'right', field: 'id', headerStyle: 'text-align:center' },
      ],
      //
      pagination: {
        page: 1,          //
        rowsNumber: 1     //
      },
      dismiss: null,  //

      //-------------------------------  ---------------------------
      expanded: false,//flag
      SelectLocation: '',//
      DirectoryLocation: '',//
      // ---------------------------------  --------------------------
      runFixed: false,//
      runModel: 'parallel',//
      runModelArray: [],//


    }
  },
  mounted () {
    //this.DetailToTestCase();
    this.getTestCaseList(1, null, true);
  },
  methods: {
    //
    DetailToTestCase () {
      let detailTestCase = JSON.parse(sessionStorage.getItem('TestCaseLocation'));
      console.log(detailTestCase)
      //
      if (!detailTestCase) {
        this.getTestCaseList(1, null, true);
      } else {
        if (detailTestCase.SelectLocation == '' || detailTestCase.SelectLocation.id == null) {
          this.getTestCaseList(1, null, true);
        } else {
          this.getTestCaseList(1, detailTestCase.SelectLocation.id, true);
          this.SelectLocation = detailTestCase.SelectLocation.id;
          this.DirectoryLocation = detailTestCase;
        }
      }
    },
    //
    openCrateTestCase () {
      this.createFixed = true;
    },
    //
    newCancel () {
      this.$refs.CSTestCase.newCancel();
      this.createFixed = false;
    },
    //
    newCreate () {
      if (!this.$refs.CSTestCase.newCreate()) { return; }
      let para = this.$refs.CSTestCase.newCreate();
      this.$q.loading.show()
      Apis.postCreateTestCase(para).then((res) => {
        console.log(res)
        this.getTestCaseList(1, this.SelectLocation.id);
        this.$q.notify({
          position: 'top',
          message: '',
          caption: '',
          color: 'secondary',
        })
        this.newCancel()
      })
    },
    //TeseCase
    getTestCaseList (page, ParentId, expandedBfalse) {
      this.$q.loading.show()
      let para = {
        parentId: ParentId || null,
        matchName: '',
        //pageSize: 50,
        page: page || 1
      }
      Apis.getTestCaseList(para).then((res) => {
        console.log(res)
        this.TestCaseList = res.data.results;
        this.pagination.page = page || 1;
        this.pagination.rowsNumber = Math.ceil(res.data.totalCount / 50);
        //
        if (expandedBfalse) {
          this.expanded = true;
          return;
        }
        this.$q.loading.hide();
      })
    },
    //Testcase
    deleteTestCase (value) {
      console.log(value)
      this.$q.dialog({
        title: '',
        message: '',
        persistent: true,
        ok: {
          push: true,
          label: ''
        },
        cancel: {
          push: true,
          label: ''
        },
      }).onOk(() => {
        this.$q.loading.show()
        //
        if (value.row.treeID == null) {
          let para = `?id=${value.row.id}`
          Apis.deleteTestCase(para).then((res) => {
            console.log(res)
            this.getTestCaseList(1, this.SelectLocation.id);
          })
        } else {
          let para = `?id=${value.row.treeID}`
          Apis.deleteTreeEntity(para).then((res) => {
            console.log(res)
            this.getTestCaseList(1, this.SelectLocation.id);
          })
        }
      })
    },
    //
    getMasterHostList () {
      Apis.getMasterHostList().then((res) => {
        console.log(res)
        this.masterHostList = res.data;
        this.$q.loading.hide()
      })
    },
    //
    getDataSourceName () {
      let para = {}
      Apis.getDataSourceName(para).then((res) => {
        console.log(res)
        this.dataSourceName = res.data;
        this.$q.loading.hide()
      })
    },
    //
    nextPage (value) {
      this.getTestCaseList(value, this.SelectLocation.id);
    },
    //TestCase
    toDetail (evt) {
      this.$router.push({
        name: 'TestCaseDetail',
        query: {
          id: evt.row.id
        },
      })
      sessionStorage.setItem('TestCaseLocation', JSON.stringify({
        SelectLocation: this.SelectLocation,
        DirectoryStructure: this.$refs.TreeEntity.getDirectoryStructure()
      }))
    },
    // ---------------------  --------------------
    //
    openRunModel () {
      console.log(this.$refs.MixedRunTest)
      //
      if (this.selected.length != 0) {
        this.$refs.MixedRunTest.openRunModel();
      } else {
        this.$q.notify({
          position: 'top',
          message: '',
          caption: '',
          color: 'red',
        })
      }
    },
    //
    closeRunModel () {
      this.getTestCaseList(1, this.SelectLocation.id);
    },
    //
    lookMasterLog (value) {
      this.$q.loading.show()
      Apis.getMasterLog({ caseId: value.row.id }).then((res) => {
        this.$q.loading.hide()
        this.$q.dialog({
          title: '',
          message: res.data,
          style: { 'width': '100%', 'max-width': '65vw', "white-space": "pre-line", "overflow-x": "hidden", "word-break": "break-all" }
        })
      })
    },
    // ---------------------  --------------------
    //
    getDirectoryLocation (data) {
      console.log(data)
      this.getTestCaseList(1, data.id)
      this.SelectLocation = data;
    },
  }
}
</script>

<style lang="scss" scoped>
.TestCase {
  position: fixed;
  width: 100%;
  height: 100%;
  //overflow: hidden;

  .btn {
    margin-right: 10px;
  }
  .q-pa-md {
    height: 100%;
  }
}
.pointer {
  cursor: pointer;
  margin-left: 20px;
  font-size: 12px;
  border-radius: 50%;
  display: inline-block;
}
</style>
<style lang="scss">
.q-table__container {
  height: 95%;
}
.q-table {
  .text-left {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.q-table--col-auto-width {
  width: 75px;
}
.new_input {
  width: 100%;
  padding: 10px 30px;
  .input_row {
    margin-bottom: 30px;
  }
}
.TreeEntity-slid-enter-active,
.TreeEntity-slid-leave-active {
  transition: all 0.3s;
}
.TreeEntity-slid-enter,
.TreeEntity-slid-leave-active {
  transform: translate3d(-3rem, 0, 0);
  opacity: 0;
}
</style>